body {
   
    .footer {
        margin-top: auto;
        width: 100%;
        z-index: 1;
        --footer-background:#243343;
        display:grid;
        position: relative;
        grid-area: footer;
        min-height:12rem;
        .bubbles {
            position: absolute;
            top:0;
            left:0;
            right:0;
            height:1rem;
            background:var(--footer-background);
            filter:url("#blob");
            .bubble {
                position: absolute;
                left:var(--position, 50%);
                background:var(--footer-background);
                border-radius:100%;
                animation:bubble-size var(--time, 4s) ease-in infinite var(--delay, 0s),
                    bubble-move var(--time, 4s) ease-in infinite var(--delay, 0s);
                transform:translate(-50%, 100%);
            }
        }
        .footer_content {
            z-index: 2;
            padding:2rem;
            background:var(--footer-background);
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            h5{
                color: #ffffff;
    text-shadow: 1px 2px 2px #000000;
    border-bottom: 0.2rem solid #ffffff61;
    padding-bottom: 0.6rem;
    padding-top: 5rem;
            }
            span{
                margin: 2rem 0;
               display: flex;
               justify-content: center;
               align-items: center;
             
               flex-wrap: wrap;
                a{
                    border-right: 0.3rem solid rgba(255, 255, 255, 0.232);
                    border-left: 0.3rem solid rgba(255, 255, 255, 0.232);
                    padding: 0 1rem;
                    cursor: pointer;
                    &:hover{
                        transform: scale(1.1);
                    }
                    margin: 1rem;
                    i{
                        margin-right: 0.5rem;
                    }
                }
            }
            h3{
                font-size: 1.5rem;
                text-align: center;
            }
            
        }
    }
}

@keyframes bubble-size {
    0%, 75% {
        width:var(--size, 4rem);
        height:var(--size, 4rem);
    }
    100% {
        width:0rem;
        height:0rem;
    }
}
@keyframes bubble-move {
    0% {
        bottom:-4rem;
    }
    100% {
        bottom:var(--distance, 10rem);
    }
}
