@import "../../style/fun";
@import "../../style/variables";
.my_work_container{
    width: 100%;
    .filter_buttons_container{
        @include flex(center,center,row,nowrap);

        margin: 5rem 0;
        .filter_buttons{
         width: 100%;
         max-width: 60rem;
        @include flex(center,center,row,wrap);
        button{
            border: none;
            margin: 1rem;
            font-size: 1.4rem;
            padding: 1rem 1.5rem;
            border-radius: 50rem;
            font-weight: bold;
            background: transparent;
            box-shadow: 2px 4px 4px black, -2px -3px 5px #212f3c;
            color: $white;
            cursor: pointer;
            &:hover{
                transform: scale(0.95);
            }
        }
        }
    }
 
    @include responsive("mobile")
    {
        padding: 0 1rem;        
        padding-bottom: 20rem;
    }
    padding-bottom: 20rem;

    .all_projects{
        width: 100%;

        @include flex(center,flex-start,row,wrap);
        .single_project{
            width: 100%;
            position: relative;
            max-width: 25rem;

            margin: 2rem 2rem;
            cursor: pointer;
            @include responsive("mobile")
            {
                
                margin: 2rem 0rem;
            }
            padding:1rem;
            box-shadow: 2px 2px 18px black;
            background: rgba(0, 0, 0, 0.225);
            -webkit-box-shadow: 2px 2px 18px black;
            -moz-box-shadow:2px 2px 18px black;
            &::after{
                content: "";
                width: 100%;
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                transition: 0.5s;
                z-index: 5;
                background: rgba(0, 0, 0, 0.625);
            }
          span{
            position: absolute;
            font-weight: bold;
            z-index: 15;
            font-size: 1.3rem;
            padding: 1rem 1.5rem;
            border-radius: 50rem;
            border: 1px solid white;
            background: rgba(0, 0, 0,0.3);
       text-transform: uppercase;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            width: max-content;
            text-shadow: 1px 1px 5px white;
            @include up_down_animation(1s);
          }
          &:hover::after{
            opacity: 0;
            z-index: -1;
          }
          &:hover span{
            opacity: 0;
            z-index: -1;
          }
            div{
                width: 100%;
                max-height: 30rem;
                padding: 0 0.5rem;
                 overflow-y: auto;
                 overflow-x: hidden;
                 &::-webkit-scrollbar{

                    background: rgba(0,0,0,0.3) !important;
                    width: 5px !important;
                  }
                  &::-webkit-scrollbar-thumb{
                    border-radius: 50px;
                    background: rgba(255, 255, 255, 0.401) !important;

                  }
                  &::-webkit-scrollbar-track{
                    background: rgba(0,0,0,0.1) !important;

                  }
                 img{

                     width: 100%;
                    
                    }
                 }

                 h3{
                    text-align: center;
                    position: relative;
                    margin-top: 3rem;
                    font-size: 1.6rem;
                    z-index: 6;
                    line-height: 3rem;
                 }

        }
        .img_light_box{
            max-width: 35rem;
          
        }
    }
}

// ---------------------
.perfect_position{
    top: 0% !important;
    left: 0% !important;
}
.video_player_container{
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 1000000;

    transition: 1s;
    top: -150%;
    left: 0;
    @include flex(center,center,row,nowrap);
    background: rgba(0, 0, 0, 0.825);
    padding: 0 2rem;
    iframe{
        position: relative;
        width: 100%;
        max-width: 100rem;
        max-height: 40rem;
        height: 100%;
        @include responsive("tablet")
        {
            max-width: 90rem;
            max-height: 30rem;

        }
        @include responsive("custom","1045px")
        {
            max-height: 35rem;

        }
        @include responsive("custom","790px")
        {
            max-height: 25rem;

        }
    
    }
    // ..............
    span{
        border: none;
        position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 3%;
    left: 3%;
    background: transparent;
    font-size: 4rem;
        cursor: pointer;
        color: white;
        &:hover{
            transform: translateX(0%)  scale(1.1);
        }
    }
 
}
.img_viewer{
    @include flex(center,center,row,nowrap);
    .image_viewer_container{
        max-width: 100rem;
    // width: 100%;
    max-height: 90vh;
    overflow: auto;
        img{       
            width: 100%;
            
        }
      }
}
.active_button{
    background: $light_blue !important;
}






