@import "../../style/fun";
@import "../../style/variables";

.home_page{
    padding: 0 1rem;

    
    @include flex(center,center,column,nowrap);
    @include set_bg(rgba(0,0,0,0.8),rgba(0,0,0,0.8),"../../assets/bg3.jpg");
    .inner_wrapper{
        @include flex(center,center,column,nowrap);
  width: 100%;
  height: 50rem;
    }
    .wavi_title_container
    {

        margin: 4rem 0 !important;
        @include responsive("tablet")
        {
            margin: 2rem 0 !important;
        }
        h1{
            font-size: $bh2;
            text-align: center;
            @include responsive("tablet")
            {
                font-size: 4.5rem;
            }
            @include responsive("mobile")
            {
                font-size: 3rem;
            }
            @include responsive("small-mobile")
            {
                font-size: 2.5rem;
            }
        }
    }

  .wavi_title_container h1:nth-child(1) {
    -webkit-text-stroke: 1px rgb(180, 180, 180) !important;
    
  }
  .wavi_title_container h1:nth-child(2) {
           color: white;
  }


    h1,h3,a{
        text-align: center;
        margin: 1rem 0;
    }
}
.home_typing_text{
    font-size: 2.5rem;
    color: $white;
    @include responsive("mobile")
    {
        
        font-size: 2rem;
    }
    @include responsive("small-mobile")
    {
        
        font-size: 1.6rem;
    }
    span{
        font-size: 2.5rem;
      color:  $light_blue;
      text-shadow: 1px 1px 5px;
      @include responsive("mobile")
      {
          
          font-size: 2rem;
      }
      @include responsive("small-mobile")
      {
          
          font-size: 1.6rem;
      }
    }
}