@import "../../../style/variables";
@import "../../../style/fun";

.admin_profile_container{
    transition: all 1s;
    #profile_form
    {
        max-width: 65rem;
        width: 100%;
        .input_field{
            @include flex(center,center,column,nowrap);
            width: 100%;
            margin: 2rem 0;
            label{
                width: 100%;
                padding: 0 0.5rem;
                text-align: left;
                margin: 1rem 0;
                font-weight: bold;
                font-size: 2rem;
            }
            input,textarea{
                
                width: 100%;
                padding: 2rem 1rem;
                border-radius: 0rem;
                background: #00bcd454;
                color: white;
                outline: none;
                border: none;
                &::placeholder{
                    color: rgb(211, 211, 211);
                }
    box-shadow: 0.1rem 0.3rem 0.4rem $light_blue, 0.1rem 0.1rem 10rem $light_blue, inset 0rem 0rem 10.1rem $light_blue;
            }
            textarea{
                min-height: 20rem;
                resize: none;
                &::-webkit-scrollbar{

                    background:$light_blue !important;
                    width: 1rem;
                  }
                  &::-webkit-scrollbar-thumb{
                    background:rgba(0, 0, 0, 0.494) !important;
                    border-radius: 5rem !important;
                  }
                  &::-webkit-scrollbar-track{
                    background:$light_blue !important;

                  }
            }
        }
    }
}


// -----------------------------
.admin_profile_container{
    @include flex(center,center,column,nowrap);
    padding:2rem 1rem ;
    padding-top: 5rem;
    }
    
    
    .admin_profile_img_container{
        max-width: 20rem;
        width: 100%;
        background: #ffffff0d;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        box-shadow: 0.1rem 0.3rem 0.4rem $light_blue, 0.1rem 0.1rem 10rem $light_blue, inset 0rem 0rem 10.1rem $light_blue;
    margin-bottom: 5rem;
       
        @include responsive("tablet")
        {
            max-width: 18rem;
    
        }
        @include responsive("mobile")
        {
            max-width: 15rem;
    
        }
        @include responsive("small-mobile")
        {
            max-width: 11rem;
    
        }
        img{
            border-radius: 50%;
    height: 20rem;
    width: 20rem;
    
        }
        label{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            z-index: 5;
            color: white;
            transition: 0.6s;
            font-size: 3.5rem;
            opacity: 0;
           
    
    @include flex(center,center,row,nowrap);
    cursor: pointer;
    &:hover{
        transform: translate(-50%,-50%) scale(1.1);
    }
    
        }
    
    }
    // -------------------------------------

    .input_field #my_social_links
    {
        width: 100%;
        @include flex(center,center,row,wrap);
        flex-wrap: wrap !important;
          flex-direction: row !important;
        .link{
            @include flex(space-between,center,row,nowrap);
         width: 100%;
         max-width: 25rem;
         margin: 1rem;
         background: #56545426!important;
         padding: 1rem 2rem !important;
         border-radius: 4rem !important;
         
         i{
        @include flex(center,center,row,wrap);
        span{
              @include flex(center,center,row,wrap);

          }
            font-size: 2rem;
         }

        }

        
        
    }
    #add_social_btn{
        padding:1.5rem 2rem;
        color: $light_blue;
        border: 0.1rem solid $light_blue;
        background: transparent;
        font-weight: bold;
        cursor: pointer;
        &:hover{

            @include hover()
        }

    }
    .link{
        position: relative !important;
       
        &:hover .remove_link{
            opacity: 1;
        }
    }
    .link_after{

        &:hover::after{
            opacity: 1;
        }
        background-color: transparent;
        &::after{
         border-radius: 4rem;
         background-color: #db2222;
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        opacity: 0;
        width: 100%;
        height: 100%;

     }
    }

    .remove_link{
        position: absolute;
        top: 50%;
        left: 50%;
        font-size: 2rem !important;
        opacity: 0;
         z-index: 5;
         cursor: pointer;
         transform: translate(-50%,-50%);
         &:hover{
            transform: translate(-50%,-50%) scale(1.1);
         }
    }


    .add_social_links_container{
    transition: all 1s;

        position: fixed;
        top: 0;
        left: 200%;
        padding: 0 1rem;
        width: 100%;
        height: 100%;
        z-index: 10;
        background: rgba(0, 0, 0, 0.889);
        @include flex(center,center,row,nowrap);
        .inner{
            width: 100%;
            max-width: 50rem;
            @include flex(center,center,row,wrap);
            div{
                margin: 1rem 0;
                width: 100%;
                @include flex(space-between,center,row,wrap);

                input{
                    margin: 1.5rem 0;
                    padding-bottom:1rem;
                   background-color: transparent;
                   outline: none;
                   border: none;
                    border-bottom: 0.1rem solid white;
                    color:white;
                    width: 100%;
                    max-width: 40rem;
                    &::placeholder{
                        color:white;
                    }

                }
                i{
                    margin: 1rem;
                    font-size: 4rem;
                    color: white;
                }
              }

           button{
            margin: 2rem 1rem;
           }

        }
    }


    #close_social_link_container{
        position: absolute;
        top: 1%;
        right: 1%;
        font-size: 3rem;
        color: white;
        cursor: pointer;
        &:hover{
            transform: scale(1.1);
        }


    }

    .perfect_position_social_container{
        top: 0% !important;
    transition: all 1s;

        left: 0% !important;
    }



#pdf_preview{
    max-width: 30rem;
    width: 100%;
    max-height: 60rem;
    height: 100%;
    embed
    {
        overflow-y: auto;
      width: 100%;
      height: 100%;
    }
}
.input_field {
    padding: 3rem 1rem;
    box-shadow: 0.1rem 0.3rem 0.4rem $light_blue, 0.1rem 0.1rem 10rem $light_blue, inset 0rem 0rem 1.1rem $light_blue;
    background: #00efff52;
}

// --------------------------------- My Skills 
.skill_container
{
   @include flex(center,center,row,wrap);
   width: 100%;

    span{

        margin: 1rem;
        
        background: #56545426!important;
        padding: 1rem 2rem !important;
        border-radius: 4rem !important;
    }
}



#add_new_skill_div{
   @include flex(center,center,column,nowrap);
   border: none;
   border-bottom: 0.1rem solid #00bcd442;
   padding-bottom: 3rem;
   border-top: 0.1rem solid #00bcd442;
   padding-top: 3rem;
   margin: 3rem 0;
    input{
        margin: 1.5rem 0;
    }
}

// .............................. My Services .......................

.input_field #admin_services_container{
   
    width: 100%;
    padding: 2rem 0.5rem;
    @include flex(center,center,row,wrap);

    .admin_service
    {
        width: 100%;
        max-width: 30rem;
        position: relative;

        box-shadow: 0.1rem 0.2rem 0.7rem #00bcd4;
        background: #0000005c;
        max-width: 20rem;
        border-radius: 3rem;
        margin: 1.5rem 1rem;
        transition: 0.5s;
        &:hover{
            transform: scale(0.95);
        }

        &:hover  .toggle_button_container
        {
            opacity: 1 !important;
        }

      .toggle_button_container{
        border-radius: 3rem;

        width: 100%;
        height: 100%;
        position: absolute;
        @include flex(center,center,row,wrap);
        background: rgba(0, 0, 0, 0.9);
        transition: 0.6s;

          opacity: 0;
          z-index: 5;
            i{
                font-size: 3rem;
                margin: 1rem;
                cursor: pointer;
                transition: 0.6s;

                &:hover{
        transition: 0.6s;

                    transform: scale(1.1);
                }
            }
            i[target_type=edit]
            {
                color:#02ef02 !important;
            }
            i[target_type=del]
            {
                color:red !important;
            }
      }

        @include flex(center,center,column,nowrap);
        padding: 2rem 1rem;
        i{
            color: $light_blue;
            font-size: 7rem;
            margin: 1rem 0;
        }
        h3{
            font-size: 1.1rem;
            word-break: break-all;
            text-align: center;
            margin: 1rem 0;
            color:$white;
        }


    }

}

// -------------------Global ...................... 

// services manager 
.services_manager{
    position: fixed !important;
    top: 0px;
    transition: 1s;
    left: -150%;
 
    width:100%;
    height:100%;

    background: rgba(0, 0, 0, 0.95);
    @include flex(center,center,row,nowrap);
    .inner{
    @include flex(flex-start,center,column,nowrap);

        max-width: 50rem;
        width: 100%;
        padding: 2rem 1rem;
        h1{
            margin-bottom:3rem ;
        }
        input,textarea{
            width: 100%;
            color: $white;
            background: rgba(0, 0, 0, 0.37);
            box-shadow: 0.1rem 0.1rem 0.4rem $light_blue;
            padding: 1.5rem 2rem;
            resize: none;
            border-radius: 1rem;
            outline: none;
            border: none;
        }
        div,input,textarea,#add_service_btn{
            margin: 2rem 0;

        }
        textarea{
            min-height: 15rem;
        }
        div{
            @include flex(center,center,row,wrap);
            i{
                margin: 0.5rem;
                font-size:5rem;
            }
            button{
                margin: 0.5rem;
                background: rgba(0, 0, 0, 0.37);
         font-size: 1.4rem;

             border: 1px solid $light_blue;
             color: $light_blue;
                padding: 1rem 1.5rem;
                cursor: pointer;
                &:hover{
                    transform: scale(1.1);
                }
            }
        }
    }

}

#close_services_manager{
    position: absolute !important;
    top: 2%;
    right: 2%;
    color: $white;
    font-size: 3rem;
    cursor: pointer;
    &:hover{
        transform: scale(1.1);
    }
   
}

.show_service_manager{
    left:0px !important;
z-index: 5;
}
