@import "../../style/fun";
@import "../../style/variables";

.admin_panel_services{
    width: 100%;
    @include flex(center,center,row,wrap);
    div{
        margin: 2rem 1rem;
        border: 0.3rem solid #ffffff5e;
        padding: 2rem 3rem;
        cursor: pointer;
        border-radius: 20rem;
        &:hover{
            transform: scale(1.1);
        }
        h3{
            font-size: 3rem;
            color: white;
            text-align: center;
            @include responsive("tablet")
            {
                font-size: 2.5rem;

            }
            @include responsive("mobile")
            {
                font-size: 2rem;

            }
            @include responsive("small-mobile")
            {
                font-size: 1.8rem;

            }
         
        }
    }
}

// --------------
.admin_setting_container{
    width: 100%;
    position: fixed;
    transition: all 1s;

    border: none;
    box-shadow: 0px 0px 0px white, inset 1px 1px 3rem #2c3e50;
    top: -150%;
    left: 0;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.966);
    overflow-y: auto;
    z-index: 100;
    transition: 0.6s;
}

#close_admin_setting{

    position: sticky;
    top: 2%;
    font-size: 2.5rem;
    color: white;
    cursor: pointer;
    &:hover{
        transform: scale(1.1);
    }
    left: 97%;

}

.admin_profile_container{
    width: 100%;
}

.perfect_position{
    top: 0% !important;
    left: 0% !important;
}

// ----------------------
#edit_update_button{

    position: sticky;
    max-width: 12rem;
    top: 1%;
    font-size: 2.5rem;
    color: $light_blue;
    cursor: pointer;
    left: 2%;
    background-color: #ffffff0f;
    border-radius: 50rem;
    text-align: center;
    @include flex(center,center,row,nowrap);
    padding: 0.5rem 2.5rem;
    border: 1px solid $light_blue;
    &:hover{
        transform: scale(1.1);
    }

}

// ----------------------------admin profile setting ----------------------------

.cond_disabled{
    cursor: pointer;

}
.cond_disabled::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.877);
    z-index: -4;
    transition: 0.5s;
    
    opacity: 0;
}
.cond_disabled:hover::after{
    opacity: 1;
    
    z-index: 4;
 }
 .cond_disabled:hover label{
    opacity: 1;
    
    z-index: 5;
 }

// -----------------------------------------------------------------------------
.empty_heading_admin{
    width: 100% !important;
    text-align: center !important;
    font-size: 3rem !important;
    color: #00b2c8 !important;
    text-shadow: 0.2rem 0.3rem 1rem !important;
    margin: 3rem 0 !important; 
  }