.perfect_display_icon_picker{

    top: 50% !important;
}

.icon_picker_root{
    box-shadow:0px 0px 15px #1db296, inset 0px 0px 20px #1db296;
position: fixed;
top: 150%;
left: 50%;
transform: translate(-50%,-50%);
width: 100%;
max-width: 130rem;
height: 80vh;
z-index:20;
background: black;
padding: 2rem;

.icon_picker_inner{
    width: 100% !important;
    height: 100% !important;
    display: flex !important;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    input{
    box-shadow:0px 0px 15px #1db296, inset 0px 0px 20px #1db296;
        &::placeholder{
            color: white;
        }
        color: white;
        width: 100% !important;
        max-width: 70rem !important;
        padding: 2rem 1.5rem !important;
        font-size: 1.6rem !important;
        background: #1db29638  !important;
        border: 0.1rem solid #1db296 !important;
        outline: none;
        border-radius: 7rem!important;
    }
}
}
.icon_picker_root .icon_picker_inner .icon_picker_container{
    display: flex;
    justify-content: center;
    align-items: start;
    flex-direction: row !important;
    flex-wrap: wrap !important;

    width: 100%;
    height: 100%;
    flex-wrap: wrap;
    overflow-y: auto;
    i{
        margin: 1.5rem;
        transition: 0.6s;
        cursor: pointer;
        &:hover{
            transform: scale(1.1);
        }
    }
}

#close_icon_picker{
    position: absolute;
    top: -7%;
    right: -1%;
    cursor: pointer;
    font-size: 4rem;
    &:hover{
        transform: scale(0.9);
    }

}