@import "../../../style/variables";
@import "../../../style/fun";




.AdminPanel_Projects{
    
    max-width: 105rem;
    width: 100%;
    margin: 0 auto;
    padding: 3rem 1rem;
    h1[target="heading"]{
        text-align: center;
       @include responsive("mobile")
       {
        font-size: 2.5rem;
       }
    }
    hr{
        max-width: 90% !important;
    margin: 3rem auto !important; 
    color: white !important;
    }
}

.project_filter_btn_container_outer{
    
    width: 100%;
    margin: 3rem 0;
    margin-top: 10rem;
    @include flex(center,center,column,nowrap);
    .project_filter_btn_container_inner
    {
        margin: 2rem 0;
        @include flex(center,center,row,wrap);
        span{
            margin: 1rem;
            
            position: relative;
            button{
                padding: 1rem 1.5rem;
                border: none;
                border-radius: 5rem;
                background: $white;
                color: black;
                font-weight: bold;
                position: relative;
                &:hover{
                    transform: scale(1.1);
                }
            }
            
            i{
                position: absolute;
                top: 50%;
                cursor: pointer;

                left: 50%;
                transform: translate(-50%,-50%);
                color: white;
                font-size: 2rem;
                opacity: 0;
                z-index: 5;

            }
           
        }
        .filter_buttons{
            
           &::after{
                border: none !important;
                 z-index: 5;
                    content:"";
                    border-radius: 5rem;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                  
                }
                &:hover i
                {
                    opacity: 1;
                  z-index: 10;
                }
              
                
                &:hover button{
                    background:transparent !important;
                    z-index: -1;
                }
                &:hover{
                    background: red;
                }
                border-radius: 5rem;
                
                
        }
    }
    input{
        width: 100%;
        max-width: 60rem;
        color: $white;
        font-size: 1.5rem;
        padding: 1.5rem 2rem;
        border: none;
        border-radius: 10rem;
        margin-bottom: 2.5rem;
        outline: none;
        box-shadow: 0.1rem 0.3rem 0.4rem $light_blue, 0.1rem 0.1rem 10rem $light_blue, inset 0rem 0rem 1.1rem $light_blue;
        background: #00efff52;
        &::placeholder{
            color: $white;

        }

    }
}


.project_container{


    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    .project_container_inner{
        margin: 3rem 0rem;
        width: 100%;
        @include flex(center,flex-start,row,wrap);
        .single_project{
            width: 100%;
            margin: 1.5rem 2rem;
            overflow: hidden;
            position: relative;
            max-width: 30rem;
            padding: 2rem;
            background: #0e0e0e;
            border-radius: 1rem;
            &:hover .edit_del_container{
                top:0% !important;
            }
            .project_img_container{
                width: 100%;
                max-height: 30rem;
                overflow-y: auto;

             img{
                width: 100%;
                height: 100%;
             }
            }
            h3{
                color: White;
                text-align: center;
                margin-top: 1rem;
            }
            .edit_del_container{
                
                position: absolute;
                background: rgba(0, 0, 0, 0.835);
                width: 100%;
                height: 100%;
                top:-100%;
                left:0;
                @include flex(center,center,row,wrap);
                border: 0.1rem solid rgba(255, 255, 255, 0.176);
                border-radius: 1rem;
                i{
                    font-size: 3rem;
                    cursor: pointer;
                    margin: 1rem;
                    color: white;
                    &:hover{
                        transform: scale(1.1);
                    }
                }
                .fa-edit{
                    &:hover{
                        color: green;
                    }
                }
                .fa-trash{
                    &:hover{
                        color: red;
                    }
                }
            }
        }
        button{
            margin: 3rem 0;
        }
    }
    h2{
        text-align: center;
        margin-bottom: 5rem;
    }
}


.portfolio_project_creater_updater{
    overflow-y: auto;
    position: fixed;
    padding: 2rem 1rem;
    width: 100%;
    height: 100%;
    max-width: 100% !important;
max-height: 100% !important;
overflow-x: hidden;
    top: 0px;
    left: -100%;
    z-index: 100;
    background: rgba(0, 0, 0, 0.938);
    @include flex(center,flex-start,row,nowrap);
    .project_creation_portal_inner{
        @include flex(flex-start,center,column,nowrap);
        width: 100%;
        max-width: 50rem;
        background-color: black;
        .img_container{
            max-width: 25rem;
            overflow-y: auto;
            max-height: 30rem;
            img{
                width: 100%;
            }
        }
           h1{
            margin: 3rem 0;
           }
        .common_input_style{
            width: 100%;
            padding: 1.5rem 2rem;
            font-size: 1.6rem;
            border-radius: 10rem;
            border: none;
            outline: none;
            color:white;
            margin: 2rem 0;
            box-shadow: 0.1rem 0.3rem 0.4rem $light_blue, 0.1rem 0.1rem 10rem $light_blue, inset 0rem 0rem 1.1rem $light_blue;
            background: #00efff52;
            &::placeholder{
                color: $white;
    
            }
        }
       label{
        text-align: center;
        max-width: 25rem;
        cursor: pointer;
        &:hover
        {
            transform: scale(1.1);
        }
       }
    }
    .close_portal_icon{
        color: $light_blue;
    font-size: 3.5rem;
    position: sticky;
    top: 1%;
    cursor: pointer;
    left: 100%;
        &:hover{
            transform: scale(0.95);
        }
    }

}
.perfect_position_projectportal{
    left: 0px !important;
}

.project_type_container{
    @include flex(center,center,row,wrap);
    width: 100%;
    span{
        width: 100%;
        display: block;
        text-align: center;
    }
   div
   {
    position: relative;
    margin: 1rem;
    i{
        display: none;
    }
     
       button{
           color: white;
           font-weight: bold;
           box-shadow: 0.1rem 0.3rem 0.4rem $light_blue, 0.1rem 0.1rem 10rem $light_blue, inset 0rem 0rem 1.1rem $light_blue;
           background: #00efff52;
           &:hover{
            transform: scale(1) !important;
          }
        }
    }
}
.project_type_container .show_project_type{
    position: relative;
    margin: 1rem;
    i{
       
        color:white !important;
        @include flex(center,center,row,nowrap);
        font-size: 2rem !important;
        position: absolute !important;
        top: 50% !important;
        left: 50% !important;
          width: 100% !important;
          height: 100% !important;
          border-radius: 50rem !important;
          background: rgba(0, 0, 0, 0.538) !important;
       
        transform: translate(-50%,-50%) !important;
     }
     button{
        color: white;
        font-weight: bold;
        box-shadow: 0.1rem 0.3rem 0.4rem $light_blue, 0.1rem 0.1rem 10rem $light_blue, inset 0rem 0rem 1.1rem $light_blue;
        background: #00efff52;
     }
}

.project_categories_container{
    width: 100%;
    padding: 2rem 1rem;
    max-width: 50rem;
    @include flex(center,center,row,wrap);
    span{
        width: 100%;
        text-align: center;
        display: block;
        font-weight: bold;
        margin: 2rem 0;
    }
    div{
        margin: 1rem;
        input{
            margin-right: 0.5rem;
        }
      
        label{
            font-size: 1.6rem;
            text-transform: capitalize;
            font-weight: bold;
        }
    }

}

#creatin_portal_caller_btn{
    margin: 0 auto;
}