@import "../../style/fun";
@import "../../style/variables";

.header_heading{
    width: 100%;
    @include flex(center,center,row,nowrap);
    min-height: 25rem;
       background: #0000004d;


    @include responsive("mobile")
    {
                min-height: 20rem;
                font-size: 3.5rem;
            }
    .wavi_title_container
    {

        margin: 4rem 0 !important;
        @include responsive("tablet")
        {
            margin: 2rem 0 !important;
        }
        h1{
            font-size: $bh2;
            text-align: center;
            @include responsive("tablet")
            {
                font-size: 4.5rem;
            }
            @include responsive("small-mobile")
            {
                font-size: 3.5rem;
            }
           
        }
    }

  .wavi_title_container h1:nth-child(1) {
    -webkit-text-stroke: 1px rgb(180, 180, 180) !important;
    
  }
  .wavi_title_container h1:nth-child(2) {
           color: white;
  }
}

.inner_container{
    padding-top: 10rem;
    width: 100%;
    padding-bottom: 2rem;
}
// -----------------------------------------------------
.sub_heading_container{
    width: 100%;
    @include flex(center,center,row,nowrap);
    padding: 3rem 0;
    margin: 5rem 0;

       background: #0000004d;


    @include responsive("mobile")
    {
    padding: 2rem 0;

                font-size: 2rem;
            }
    .wavi_title_container
    {

        @include responsive("tablet")
        {
            margin: 2rem 0 !important;
        }
        h1{
            font-size: $bh;
            text-align: center;
            @include responsive("tablet")
            {
                font-size: 3rem;
            }
           
           
        }
    }

  .wavi_title_container h1:nth-child(1) {
    -webkit-text-stroke: 1px rgb(180, 180, 180) !important;
    
  }
  .wavi_title_container h1:nth-child(2) {
           color: white;
  }

}
.skills_container{
    width: 100%;
    padding: 2rem 1rem;
    padding-bottom: 20rem;
    @include flex(space-around,center,row,wrap);
}
.about_use_Container{
    position: relative;
    transition: 1.5s;
    width: 100%;
    height: auto;
    max-height: 3rem;

    max-width: 35rem;
    border-radius: 1rem;
    &:hover{

        
        max-width: 80rem;
        max-height: 150rem;
        @include responsive("mobile")
        {
            max-height: 3000rem;

        }
       }
    padding: 5rem 2rem;
    padding-top: 15rem;
    background: rgba(0,0,0,0.35);
    @include flex(flex-start,center,column,nowrap);

    margin: 0 auto;
    margin-top: 12rem;
    .profile_container{
        @include flex(flex-start,center,column,nowrap);
        position: absolute;
        z-index: 3;
        transform: translateY(-27rem);
        width: 100%;
        .img_container{
            width: 100%;
            max-width: 20rem;
            img{
                border: 1.5rem solid $background_color;
                width: 100%;
                border-radius: 50%;
    width: 20rem;
    height: 20rem;
                
            }
        }
        h1{
            font-size: 2rem;
            margin-top: 1.5rem;
        }
        h3{
            font-size: 1.1rem;
            font-weight: 100;
            max-width: 20rem;
            text-align: center;
            margin-top: 1rem;
            word-break: break-word;
        }
    }
    .content{
        overflow: hidden;
    transition: 10s;

        opacity: 0;
        p{
            text-align: center;
        }
        margin: 2rem 0;
        margin-top: 3rem;
    }
    .btn_container{
        overflow: hidden;
        opacity: 0;

        transition: 4s;

        margin: 2rem 0;
        @include flex(space-around,center,row,wrap);
        width: 100%;
        max-width: 40rem;
        a,button{
         margin:  1rem;
        }

    }
}



.about_use_Container:hover .btn_container,.about_use_Container:hover .content
{
    opacity: 1;

}