@import "../../style/variables";
@import "../../style/fun";
.loading_container{
    width: 100% !important;
    padding: 10rem 0 !important;
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
    flex-direction: row !important;
    flex-wrap: nowrap !important;
    .loading_outer{
      display: flex !important;
      justify-content: center !important;
      align-items: center !important;
      flex-direction: row !important;
      flex-wrap: nowrap !important;
      position: relative !important;
      width: 20rem !important;
      height: 20rem !important;
      .loading_inner{
        position: absolute !important;
        width: 100% !important;
        margin: 0 !important;
        padding: 0 !important;
        height: 100% !important;
        top: 0% !important;
        left: 0% !important;
        border-radius: 50% !important;
        border-top: 1rem solid $light_blue !important;
        box-shadow: 0.1rem 0.1rem 0.1rem $light_blue,inset 0.1rem 0.1rem 0.5rem $light_blue !important;
        animation: loading_rotation 2s linear 0s infinite  ;
      }
      // background: rgba(0, 0, 0, 0.1);
     
      h3{
         color: $white;
         font-size: 0.9em !important;
       }
    }
  }
  @keyframes loading_rotation {
    0%{
      transform: rotate(0deg);
      
    }
   
    100%{
  
      transform: rotate(360deg);
  
    }
    
  }