@import "../../style/fun";
@import "../../style/variables";
#main_navbar,#main_navbar_rs
{
 position: fixed;
 width: 20%;
 top: 0px;
 left: 0px;
 overflow-y: auto;
 height: 100vh;
 max-height: 100%;
 &::-webkit-scrollbar{

    background: rgba(0,0,0,0.3) !important;
    width: 5px !important;
  }
  &::-webkit-scrollbar-thumb{
    border-radius: 50px;
    background: rgba(255, 255, 255, 0.401) !important;

  }
  &::-webkit-scrollbar-track{
    background: rgba(0,0,0,0.1) !important;

  }
 .brand_container{
    margin-top: 6rem;
    width: 100%;
    
    @include responsive("tablet")
    {
           margin-top: 1rem;

       }
  

   @include flex(center,center,column,nowrap);
   .brand_img_container{
    width: 100%;
    max-width: 10rem;
    img{
        max-width: 100%;
        width: 100%;
        border-radius: 100%;
        border: 0.6rem solid #fffdfd;
        margin-bottom: 1rem;
    }
   }
   h3{
    color: $white;
     text-align: center;
     padding: 0 0.5rem;
     
   }
 }
}
#main_navbar,#main_navbar_rs
{
    @include flex(flex-start,center,column,nowrap);
  transition: 1s;

    .nav_links_container{
        margin-top: 4rem;
        @include flex(center,center,column,nowrap);
        width: 100%;
        a{
            width: 100%;
            margin: 0.5rem 0;
            transition: 1s;
            font-family: 'Cinzel Decorative', cursive;
            font-weight: 700;
    font-size: 1.8rem;
padding: 0.6rem 0;
    text-align: center;
            &:hover{
                transform: scale(0.9);
                    background: rgba(0, 0, 0, 0.626);
            }

            color: $white;       
        }
    }
    
}
#main_navbar{
    @include responsive("tablet")
    {
        display: none;
    
    }
}
#main_navbar_rs{
    display: none !important;
    @include responsive("tablet")
    {
 background: $gray;

        display: flex !important;
      width: 26rem;
      z-index: 20;
        left: -150%;
    }
}
.nav_social_icons{
    margin-top: auto;
    padding-bottom: 2rem;
    @include flex(center,center,row,wrap);
    a{
        
        font-size: 2.2rem;
        margin: 0 0.5rem;
       
        padding: 0.5rem;
        box-shadow: -2px -1px 6px #181818;
        @include flex(center,center,row,nowrap);
        &:hover{

        transform: scale(1.2);    
        }


    }
}


.nav_toggle_btn{
    font-size: 2.5rem;
    color: white;
    position: absolute;
    top: 1%;
    right:2%;
}

.active_page{
    color: $light_blue !important;
    background: rgba(0, 0, 0, 0.626);

}

#dropdown_navbar{
    width: 100%;
    padding: 1rem 2rem;
    z-index: 10;
    position: fixed;
    background: #000000c7;
    display: none;
    .logo_container{
        width: 100%;
        h3{
            font-size: 1.8rem;
            color: white;
            font-family: 'Kalam', cursive;
            
            span{
                font-size: 1.8rem;
                
                color: $light_blue;
            }
        }

        #toggle_nav_btn{
            font-size: 2.5rem;
            color: white;

        }
    }


    @include responsive("tablet")
    {
        @include flex(space-between,center,row,nowrap);

    }
}