@import "./fun";
@import "./variables";
@import url('https://fonts.googleapis.com/css2?family=Aboreto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kalam:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700&display=swap');
*{
    margin: 0;
    padding: 0;
    text-decoration: none;
    transition: 0.6s;
      box-sizing: border-box !important;
    list-style: none;
}
html{
    font-size: 10px !important;
}
body{
    height: 100vh;
    width: 100%;
    background: $background_color;
    // background: $dark;

}
 #root
{
    width: 100%;
    min-height: 100vh;
}
a,p,div,textarea,span{
    font-size: 1.7rem;
    color: $white;
    font-family: 'Kalam', cursive;
    letter-spacing: 0.15rem;
}
h1,h2,h3,h4,h5,h6{
    font-family: 'Aboreto', cursive;
    font-family: 'Cinzel Decorative', cursive;
    font-weight: 700;
}
h1{
    font-size:3rem;
}
h2{
    font-size:2rem;
}
h3{
    font-size:1.8rem;
}



@media screen and (min-width:850px)
{
  ::-webkit-scrollbar{

    background: rgb(255, 255, 255) !important;
    width: 10px;
  }
  ::-webkit-scrollbar-thumb{
    background:linear-gradient(45deg,rgb(168, 168, 168),rgb(72, 71, 66))!important;
    border-radius: 50px;
  }
  ::-webkit-scrollbar-track{
    background: rgb(21, 21, 21) !important;
  }
}



.page_container
{
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    overflow-x:hidden;
    position: fixed;
    top: 0;
    left: 20%;
    width: 80%;
    height: 100vh;
    max-height: 100vh;
    z-index: 9;
    @include responsive("tablet")
    {
       width: 100%;
       left: 0;

    padding-top: 5rem;

    }
}
.dnone{
  display: none !important;
}
.onone{
opacity: 0 !important;
margin: 0 !important;
padding: 0 !important;
z-index: -1;
transition: 1s;
}
.empty_heading{
  color: #0000008c;
  color: #0000008c;
  letter-spacing: 1rem;
  font-size: 8rem;
  text-align: center;
  margin: 2rem 0;
  span{
      color: #0000008c;
      font-size: 8rem;
         
  }
  @include responsive("tablet")
  {
      font-size: 6rem;
      span{
          font-size: 6rem;
      }

  }
  @include responsive("mobile")
  {
      font-size: 4rem;
      span{
          font-size: 4rem;
      }
  }
  @include responsive("small-mobile")
  {
      font-size: 3rem;
      span{
          font-size: 3rem;
      }
  }
}
.nbtn{
  padding: 1rem 1.5rem !important;
  color: $light_blue !important;
  border: 0.1rem solid #00bcd4 !important;
  background: transparent !important;
  font-weight: bold !important;
  width: auto !important;
  border-radius: 6rem !important;
  cursor: pointer;
  font-size: 1.5rem !important;
        &:hover{

            @include hover()
        }
}
.cbtn_small{
  box-shadow: 0px 0px 5px $light_blue, inset 0px 0px 16px $light_blue;
    background: #ffffff0f;
    text-shadow: 1px 1px 1px black;

    padding: 1.3rem 2rem;
    font-family: 'Aboreto', cursive;
    
    background: transparent;
    border: 1px solid $light_blue;
    color: $light_blue;
    font-size: 1.5rem;
    font-weight: bold;
    border-radius: 50rem;
  cursor: pointer;
  &:hover{
    transform: scale(1.1);
  }
    @include responsive("mobile")
    {
    font-size: 1.4rem;

    }
}
.cbtn{
  box-shadow: 0px 0px 5px, inset 0px 0px 16px;
    background: #ffffff0f;
    text-shadow: 1px 1px 1px black;

    padding: 1.5rem 2rem;
    font-family: 'Aboreto', cursive;
    
    background: transparent;
    border: 1px solid $light_blue;
    color: $light_blue;
    font-size: 1.8rem;
    font-weight: bold;
    border-radius: 50rem;
  cursor: pointer;
  &:hover{
    transform: scale(1.1);
  }
    @include responsive("mobile")
    {
    font-size: 1.4rem;

    }
}
.flex_cc{
  @include flex(center,center,row,wrap);
}
// responsive classes 
.nav_rs{
    left: 0px !important;
}
// animation classes 


.animate_btn{
  animation: btn_animation 1s linear 0s infinite alternate;
   &:hover{
    -webkit-animation-play-state:paused;
  -moz-animation-play-state:paused;
  -o-animation-play-state:paused;
  animation-play-state:paused;
  cursor: pointer;
   }
}
@keyframes  btn_animation
{
  0%{
    transform: scale(1);
  box-shadow: 0px 0px 5px, inset 0px 0px 10px;

  }
  100%{
  box-shadow: 0px 0px 15px, inset 0px 0px 16px;

    transform: scale(1.04);

  }
  
}

// ----------------------------------
.wavi_title_container{
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
  .wavi_title_container h1{
    position: absolute;
    font-family: 'Cinzel Decorative', cursive;
    font-weight: 700;
  }
  .wavi_title_container h1:nth-child(1) {
    z-index: 5;
    color: transparent !important;
    // -webkit-text-stroke: 1px $light_blue !important;
  }
  .wavi_title_container h1:nth-child(2) {
    z-index: 2;
    // color: $light_blue;
    animation-name: title_wavi;
    
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    animation-direction: alternate;
   
  }
  .temp_text{
    visibility: hidden;
  }
  
  @keyframes title_wavi {
  
    0%,100%{

      clip-path: polygon(0% 100%, 1% 59%, 6% 46%, 35% 42%, 54% 46%, 82% 69%, 100% 75%, 100% 100%);
    }
    50%{

      clip-path: polygon(0% 100%, 1% 79%, 18% 70%, 30% 54%, 45% 42%, 73% 36%, 100% 41%, 100% 100%);
    }
    
    
  }


  .progress_bar_container{
    @include flex(center,center,column,nowrap);
    width: 100%;
    margin: 2rem 1rem;
    max-width: 40rem;
    h3{
      width: 100%;
      text-align: left;
      margin: 1rem 0;
    }
    .progress_bar_outer{
      width: 100%;
      height: 2rem;
      position: relative;
      border-radius: 50rem;
      background: #00000000;
      box-shadow: 1px 1px 9px $light_blue, inset 1px 1px 8px $light_blue;
      span{
        position: absolute;
        top: -3rem;
        right: 1%;

      }
      .progress_bar_inner{
        // width: 50%;
      border-radius: 50rem;

        transition: 2s;
        height: 100%;
        background: $light_blue;

      }
    }
  }
.perfect_position_progress_bar{
  top: 2% !important; 

}
  .file_upload_progress_bar{
    width: 100% !important;
    position: fixed;
    background: #000000a8;
    min-height: 10rem;
    border-radius: 1rem;
    padding: 2rem;
  top: -150% ; 
  transition: 1.5s;

    left:50%;
    transform: translateX(-50%);
    max-width: 40rem !important;

    z-index: 200;
    @include flex(center,center,row,nowrap);
    transition: 1s !important;
    .outer{
      transition: 1s !important;
      display: flex !important;
      justify-content: flex-start !important;
      align-items: center !important;
      position: relative;
      
      width: 100% !important;
      height: 1rem !important;
      border-radius: 5rem !important;
      padding: 0 !important;
      margin: 0 !important;
      
      background: #000000 !important;
      box-shadow: 0.1rem 0.3rem 0.6rem $light_blue, inset 0.1rem 0.2rem 0.9rem $light_blue !important;
      
      
      h5,h6{
        padding: 0 !important;
        margin: 0 !important;
        position: absolute !important;
        font-size: 1.4rem;
        z-index: 20;
      }
        h5{
          top: -320%;
          right: 1%;
        }
        h6{
          left: 1%;
          top: -320%;

        }
      .inner{
        transition: 1s !important;
        border-radius: 5rem !important;
        margin: 0 !important;
        padding: 0 !important;
        position: absolute;
        top: 0;
        left: 0;
        height: 100% !important;
        background: $light_blue !important;
      }
    }
  }

